import { Navigation, WidthBoundary } from '@/components';
import styled from '@emotion/styled';
import { Button, TextField } from '@mui/material';
import React, { useState } from 'react';
import { colors } from '@/theme';

interface Props {
  location: Location;
}
const InputField = styled(TextField)`
  .MuiInputBase-root {
    background-color: ${colors.background};
  }
  width: 100%;
  margin-top: 16px;
  margin-bottom: 16px;
  .Mui-error {
    font-weight: 500;
  }
`;
const {
  Form,
  InputFieldsContainer,
  NameFields,
  NameField,
  SubmitButton,
  Title,
} = {
  Form: styled.form`
    display: flex;
    flex-direction: column;
    justify-content: center;
    width: 55%;
    margin: auto;
    @media only screen and (max-width: 767px) {
      width: 80%;
    }
  `,
  InputFieldsContainer: styled.div`
    margin-bottom: 32px;
  `,
  NameFields: styled.div`
    display: flex;
    flex-direction: row;
    @media only screen and (max-width: 767px) {
      flex-direction: column;
    }
  `,

  NameField: styled(InputField)`
    @media only screen and (min-width: 768px) {
      flex: 1;
      margin-top: 0;
      :first-of-type {
        margin-right: 8px;
      }
      :last-of-type {
        margin-left: 8px;
      }
    }
  `,
  SubmitButton: styled(Button)`
    background-color: ${colors.primary};
  `,
  Title: styled.h2`
    text-align: center;
  `,
};

type FormInput = { value?: string; errorMessage?: string };

type InputFields =
  | 'firstName'
  | 'lastName'
  | 'tel'
  | 'email'
  | 'weddingDate'
  | 'message';

type FormState = Record<InputFields, FormInput>;

type ValidForm = Record<InputFields, Required<FormInput>>;

const validateRequired = (value?: string) =>
  !value ? `Dit veld is verplicht` : ``;

const validateEmailFormat = (value: string) =>
  !/^\S+@\S+$/.test(value) ? `Dit is geen geldig e-mailadres` : ``;

const validateForm = (form: FormState): { [key in InputFields]: string } => ({
  firstName: validateRequired(form.firstName?.value),
  lastName: validateRequired(form.lastName?.value),
  tel: validateRequired(form.tel?.value),
  weddingDate: ``,
  email:
    validateRequired(form.email?.value) ||
    validateEmailFormat(form.email.value!),
  message: validateRequired(form.message?.value),
});

const isValidForm = (
  formErrors: ReturnType<typeof validateForm>,
  formState: FormState,
): formState is ValidForm =>
  !Object.values(formErrors).find((fieldError) => !!fieldError);

const sendMessage = (form: ValidForm) => {
  const url = new URL(`https://www.byvtphotography.nl/send_mail.php`);
  Object.entries(form)
    .filter(([_, { value }]) => !!value)
    .forEach(([key, { value }]) => url.searchParams.append(key, value));
  console.log(url);

  fetch(url).catch((err) => console.warn(err));
};

export const ContactForm: React.FC = () => {
  const [formState, setFormState] = useState<FormState>({
    firstName: {},
    lastName: {},
    tel: {},
    weddingDate: {},
    email: {},
    message: {},
  });
  console.log(formState.firstName.value);
  const onSubmit: React.FormEventHandler<HTMLFormElement> = (event) => {
    event.preventDefault();
    console.log(`onClick`);
    const formErrors = validateForm(formState);
    console.log(`formErrors`, formErrors);
    if (isValidForm(formErrors, formState)) {
      sendMessage(formState);
    } else {
      setFormState({
        firstName: {
          ...formState.firstName,
          errorMessage: formErrors.firstName,
        },
        lastName: {
          ...formState.lastName,
          errorMessage: formErrors.lastName,
        },
        tel: { ...formState.tel, errorMessage: formErrors.tel },
        email: {
          ...formState.email,
          errorMessage: formErrors.email,
        },
        weddingDate: {
          ...formState.weddingDate,
        },
        message: {
          ...formState.message,
          errorMessage: formErrors.message,
        },
      });
    }
  };

  const handleInputChange = (
    event: React.ChangeEvent<HTMLInputElement | HTMLTextAreaElement>,
  ) => {
    const { name, value } = event.target;
    setFormState({
      ...formState,
      [name]: { value, errorMessage: `` },
    });
  };

  return (
    <Form onSubmit={onSubmit}>
      <InputFieldsContainer>
        <NameFields>
          <NameField
            name="firstName"
            label="Voornaam"
            value={formState.firstName.value}
            defaultValue=""
            onChange={handleInputChange}
            error={!!formState.firstName.errorMessage}
            helperText={formState.firstName.errorMessage}
          />
          <NameField
            fullWidth
            name="lastName"
            label="Achternaam"
            value={formState.lastName.value}
            onChange={handleInputChange}
            error={!!formState.lastName.errorMessage}
            helperText={formState.lastName.errorMessage}
          />
        </NameFields>
        <InputField
          type="tel"
          name="tel"
          label="Mobiel"
          value={formState.tel.value}
          onChange={handleInputChange}
          error={!!formState.tel.errorMessage}
          helperText={formState.tel.errorMessage}
        />
        <InputField
          type="email"
          name="email"
          label="E-mail"
          value={formState.email.value}
          onChange={handleInputChange}
          error={!!formState.email.errorMessage}
          helperText={formState.email.errorMessage}
        />
        <InputField
          name="weddingDate"
          label="Trouwdatum"
          value={formState.weddingDate.value}
          onChange={handleInputChange}
        />
        <InputField
          multiline
          minRows={3}
          maxRows={6}
          name="message"
          label="Bericht"
          onChange={handleInputChange}
          value={formState.message.value}
          error={!!formState.message.errorMessage}
          helperText={formState.message.errorMessage}
        />
      </InputFieldsContainer>
      <SubmitButton variant="contained" type="submit">
        Verstuur Bericht
      </SubmitButton>
    </Form>
  );
};

export default function Contact({ location }: Props) {
  return (
    <main>
      <header>
        <Navigation location={location} showCTA={false} />
      </header>
      <WidthBoundary
        backgroundStyle={{
          backgroundColor: colors.backgroundTint,
          paddingBottom: `32px`,
          paddingTop: `32px`,
        }}
      >
        <Title>Contact formulier</Title>
        <ContactForm />
      </WidthBoundary>
    </main>
  );
}
